@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  
}
.light {
  --bg-colour: white;
  --column-colour: #F3F3F3;
  --task-colour: white;
  --text-colour: black;
  --task-highlight-colour: lightgreen;
  --column-highlight-colour: lightblue;
}
.dark {
  --bg-colour: #434343;
  --column-colour: #333333;
  --task-colour: #434343;
  --text-colour: #D1D1D1;
  --task-highlight-colour: darkgreen;
  --column-highlight-colour: #000069;
}

.app-container {
  background-color: var(--bg-colour);
  font-family: 'Lato', sans-serif;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: flex-start;
  color: var(--text-colour);
}

.app-inner-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.theme-button {
  padding: 0.5rem;
  margin: 1rem;
  font-size: 1rem;
  border: 1px solid lightgrey;
  border-radius: 4px;
  color: var(--text-colour);
  background-color: var(--column-colour);
}
.theme-button:focus {
  outline: none;
}
.column-container {
  margin: 0.5rem;
  border: 1px solid lightgrey;
  /* background-color: #F3F3F3; */
  background-color: var(--column-colour);
  border-radius: 2px;
  width: 220px;
  display: flex;
  flex-direction: column;
}

.column-title {
  padding: 0.5rem;
}

.column-task-list {
  padding: 0.5rem;
  margin: 0.01rem;
  flex-grow: 1;
  min-height: 50px;
}
.column-task-list-highlight {
  background-color: var(--column-highlight-colour);
}

.task-container {
  border: 1px solid lightgrey;
  border-radius: 2px;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  background-color: var(--task-colour);
  /* display: flex; */

}

.task-container-highlight {
  background-color: var(--task-highlight-colour);
}

